import { Component, OnInit } from '@angular/core';
import { SettingsService } from '../service/SettingsService';
import {SettingsBean } from '../typeBean/SettingsBean';
import { SocialLoginModule, AuthServiceConfig, GoogleLoginProvider,AuthService } from "angular5-social-login";
import { environment } from '../../environments/environment';
@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.css'],
  providers: [SettingsService]
})
export class SettingsComponent implements OnInit {

  constructor(private settingsService: SettingsService,private socialAuthService: AuthService) { 
    this.appUrl = environment.appUrl?environment.appUrl:"";
  }

appUrl = "";
public loaded:boolean = false;
public settings:SettingsBean = new SettingsBean();
public settingsSaveSuccess:boolean = false;
public isTestingModeOn:boolean = false;

public signinWithGoogle () {
  let socialPlatformProvider = GoogleLoginProvider.PROVIDER_ID;

  this.socialAuthService.signIn(socialPlatformProvider).then(
    (userData) => { //on success
       //this will return user data from google. What you need is a user token which you will send it to the server
       console.log(userData);
    }
  );
}

private getSettings(): void {
  this.loaded = false;
  this.settingsService.getSettings().then(response => {
    this.settings = response as SettingsBean;
    this.loaded = true;
    this.isTestingModeOn = response.testingmode == 'true';
  })
  .catch(err=>{
    this.loaded = true;
  });   
}

private getAuthServiceConfigs() {
  /* let config = new AuthServiceConfig([{
      id: GoogleLoginProvider.PROVIDER_ID,
      provider: new GoogleLoginProvider("<GoogleClientIdJHere>")
  }]);
  
  return config; */
}

private sendAlertsNow(){
  this.settingsService.sendAlertsNow().then(()=>{}); 
}

private saveSettings(){
  this.settings.testingmode = this.isTestingModeOn;
    this.settingsService.setSetting(this.settings).then(()=>{
      this.settingsSaveSuccess = true;
      setTimeout(()=>{this.settingsSaveSuccess = false;},2000);
    });  
}



  ngOnInit() {
   this.getSettings();
  }

}



