import { Component,OnInit } from '@angular/core';
import { LoginService } from '../../service/loginService';
import { SocialLoginModule, AuthServiceConfig, GoogleLoginProvider,AuthService } from "angular5-social-login";
//import {GoogleLoginProvider} from '../../services/GoogleLoginProviderExt'

@Component({
  selector: 'Login-Page',
  templateUrl: './app.edit.html', 
  providers: [LoginService]
})
export class EditUser implements OnInit{
	constructor(private loginService: LoginService,private socialAuthService: AuthService) {
		
	}
	
	userData = {};
	hasError = false;
	password = "";
	handleError(err:any):void{
		window.location.href="/"
	}

	public signinWithGoogle() {
		let socialPlatformProvider = GoogleLoginProvider.PROVIDER_ID;
	
		this.socialAuthService.signIn(socialPlatformProvider).then(
			(userData) => {
				 this.userData['googleId'] = userData.email;
				 this.userData['image'] = userData.image;
			}
		);
	}

	getLoggedInUser():void{
		this.loginService.loggedInUser().then(userDatan => {
		  console.log(userDatan);
		  this.userData = userDatan;
		}).catch(err=>{this.handleError(err);this.hasError = true;});
	}

	updateUserData(){
		//Deleting this field user
		this.userData['fullUser']=undefined;
		if(this.password.trim() == ""){
			this.userData['pass']=undefined;
		}else{
			this.userData['pass'] = this.password;
		}
		console.log(this.userData);
		this.loginService.updateUser(this.userData);
	}
	
	ngOnInit(): void {
		var that = this;
		this.getLoggedInUser();
	}
	  
}



