import { GoogleLoginProvider,SocialLoginModule,AuthServiceConfig,LoginProviderClass } from "angular5-social-login";
//import {GoogleLoginProvider} from './GoogleLoginProviderExt.js';
 
export function getAuthServiceConfigs() { 
    
    let config = new AuthServiceConfig([{
        id: GoogleLoginProvider.PROVIDER_ID,
        provider: new GoogleLoginProvider('268978747144-h5u6uplbojevsappcklaq67l4fdfjt0a.apps.googleusercontent.com')
    }]);
     
    return config;
}

